const { setConfiguration, Engine, RuleStorage, StringRuleList, Request } = require("@adguard/tsurlfilter");

async function start() {
    const easylist = await (await fetch("https://raw.githubusercontent.com/easylist/easylist/master/easylist/easylist_adservers.txt")).text();
    const list = new StringRuleList(0, easylist, false, false);
    const ruleStorage = new RuleStorage([list]);

    const config = {
        engine: "extension",
        version: "1.0.0",
        verbose: true
    }

    setConfiguration(config);

    const engine = new Engine(ruleStorage);
    console.log(engine.matchRequest(new Request("https://googleapi.club")));
}
start();